import { DataModelFactory, DataModelType } from '../core/DataModel.js'
import { Billboard } from './Billboard.js'
import { HotPointType } from '../core/Type.js'

// function HotPoint(cameraControl,canvas,position){
function HotPoint (cameraControl, position) {
  // Parent constructor //////////////////////////////////////////////
  Billboard.call(this, cameraControl)

  var scope = this

  // Public variables ////////////////////////////////////////////////
  this.insideCircle = new Billboard()
  this.outsideCircle = new Billboard()

  // publicpublic
  // publicposition.x
  // 

  // 
  this.cameraPos = new THREE.Vector3()
  this.cameraTarget = new THREE.Vector3()

  // 
  DataModelFactory.call(this, DataModelType.HOTPOINT)

  // 
  // 
  this.name = 'hotpoint'

  this.position.copy(position)
  this.outsideCircle.position.copy(position)
  this.insideCircle.position.copy(position)
  this.insideCircle.scaleFactor = 0.8

  // 
  if (checkNone(cameraControl) || !(cameraControl instanceof THREE.OrbitControls)) {
    this.cameraPos.set(0, 0, 10)
    this.cameraTarget.set(0, 0, 0)
  } else {
    this.cameraPos.copy(cameraControl.object.position)
    this.cameraTarget.copy(cameraControl.target)
  }

  // UI/
  // 
  // this.update()

  if (this.style === HotPointType.PLUS) {
    this.setTexture('static/default/images/hpPlus.png')
    this.insideCircle.setTexture('static/default/images/circle.png')
    this.outsideCircle.setTexture('static/default/images/circle.png')
  } else if (this.style === HotPointType.POINT) {
    this.setTexture('static/default/images/hpPoint.png')
    this.insideCircle.setTexture('static/default/images/circle.png')
    this.outsideCircle.setTexture('static/default/images/circle.png')
  } else if (this.style === HotPointType.EYE) {
    this.setTexture('static/default/images/hpEye.png')
    this.insideCircle.setTexture('static/default/images/circle.png')
    this.outsideCircle.setTexture('static/default/images/circle.png')
  }

  this.material.color.set(0x333333)

  this.scaleFactor = 0.4

  this.insideCircle.scaleFactor = 0.67
  this.insideCircle.material.color.set(this.dataModel.insideColor)

  this.outsideCircle.material.opacity = 0.35
  this.outsideCircle.material.color.set(this.dataModel.outsideColor)

  var bindClick = this.onClick.bind(this)
  this.insideCircle.onClick = bindClick

  this.insideCircle.isCusReturn = true
  this.insideCircle.cusReturn = function () {
    return scope
  }

  this.outsideCircle.isCusReturn = true
  this.outsideCircle.cusReturn = function () {
    return null
  }
}

HotPoint.prototype = Object.assign(Object.create(Billboard.prototype), {

  constructor: HotPoint,

  isHotPoint: true,

  isCustom: true,

  update: function () {
    Billboard.prototype.update.call(this)

    if (!checkNone(this.insideCircle)) { this.insideCircle.position.setFromMatrixPosition(this.matrixWorld) }
    if (!checkNone(this.outsideCircle)) { this.outsideCircle.position.setFromMatrixPosition(this.matrixWorld) }
  },

  addToScene: function (scene, shutSelect) {
    Billboard.prototype.addToScene.call(this, scene, shutSelect)

    this.scene = scene
    scene.add(this.insideCircle)
    scene.add(this.outsideCircle)
  },

  removeFromScene: function (shutSelect) {
    Billboard.prototype.removeFromScene.call(this, shutSelect)

    this.scene.remove(this.insideCircle)
    this.scene.remove(this.outsideCircle)
  },

  setColor: function () {
    this.material.color.set(this.color)
    this.material.needsUpdate = true
  },

  // UI
  onClick: function () {
    // 
    this.cameraControl.moveTo(this.cameraTarget, this.cameraPos)
  },

  onSelect: function () {
    this.material.color.set(0xffffff)
    this.insideCircle.material.color.set(0x0999ff)
    this.outsideCircle.material.color.set(0x0666dd)
  },

  onDeselect: function () {
    this.material.color.set(0x333333)
    this.insideCircle.material.color.set(this.dataModel.insideColor)
    this.outsideCircle.material.color.set(this.dataModel.outsideColor)
  },

  // ,cameracamerapositionlookAt
  setCameraPos: function () {
    // cameraPoscameraTarget
    this.cameraPos.copy(this.cameraControl.object.position)
    this.cameraTarget.copy(this.cameraControl.target)
  },

  setStyle: function (style) {
    switch (style) {
      case HotPointType.PLUS:
        this.setTexture('static/default/images/hpPlus.png')
        break
      case HotPointType.POINT:
        this.setTexture('static/default/images/hpPoint.png')
        break
      case HotPointType.EYE:
        this.setTexture('static/default/images/hpEye.png')
        break
    }

    this.style = style
  },

  setInsideColor: function (colorStr) {
    this.insideCircle.material.color.set(colorStr)
    this.dataModel.insideColor = colorStr
  },

  setOutsideColor: function (colorStr) {
    this.outsideCircle.material.color.set(colorStr)
    this.dataModel.outsideColor = colorStr
  },

  setOpacity: function (opacity) {
    if (opacity > 0.99) { opacity = 1.0 } else if (opacity < 0) { opacity = 0 }

    this.dataModel.opacity = opacity
    this.material.opacity = opacity
    this.insideCircle.material.opacity = opacity
    this.outsideCircle.material.opacity = 0.35 * opacity
  },

  updateFromDataModel: function () {
    this.insideCircle.position.copy(this.position)
    this.outsideCircle.position.copy(this.position)
    this.setStyle(this.style)
    this.setInsideColor(this.insideColor)
    this.setOutsideColor(this.outsideColor)
    this.setOpacity(this.opacity)
    this.material.color.set(0x333333)
  },

  show: function () {
    this.visible = true
    this.insideCircle.visible = true
    this.outsideCircle.visible = true
  },

  hide: function () {
    this.visible = false
    this.insideCircle.visible = false
    this.outsideCircle.visible = false
  }
})

function HotPointUI () {
  // Parent constructor //////////////////////////////////////////////
  // 
  DataModelFactory.call(this, DataModelType.HOTPOINT_UI)

  var scope = this

  this.remove = function () {
    this.hpImageUIList.splice(0, this.hpImageUIList.length)
    this.hpTextUIList.splice(0, this.hpTextUIList.length)
    this.hpVideoUIList.splice(0, this.hpVideoUIList.length)
    this.hiddenPageUIList.splice(0, this.hiddenPageUIList.length)
  }
}

HotPointUI.prototype.copy = function (hotPointUI) {
  this.hpImageUIList.splice(0, this.hpImageUIList.length)
  this.hpTextUIList.splice(0, this.hpTextUIList.length)
  this.hpVideoUIList.splice(0, this.hpVideoUIList.length)
  this.hiddenPageUIList.splice(0, this.hiddenPageUIList.length)
}

HotPointUI.prototype.clone = function () {
  var ui = new HotPointUI()
  ui.dataModel.copy(this.dataModel)
  return ui
}

HotPointUI.prototype.toJSON = function () {
  return this.dataModel
}

export { HotPoint, HotPointUI }



