  this.play = function (timePoint, firstLoop) {
    if (timePoint < 0 || timePoint > this.frameNumber) {
      console.log('Animation, play(), timePoint: "Must in range."')
      return 1
    }

    var res = false
    var loop = false
    for (var index = 0; index < scope.changeList.length; ++index) {
      var animaChange = scope.changeList[index]
      if (firstLoop || animaChange.loop) {
        res = res | animaChange.playKeyFrame(timePoint)
        loop = loop | animaChange.loop
      }
    }

    scope.curTimePoint = timePoint

    // 
    if (timePoint === scope.frameNumber || !res) {
      if (loop) {
        scope.curTimePoint = 0
        return 2
      }
      scope.curTimePoint = parseInt(timePoint)
      return 1
    }
    return 0
  }

  this.setAnimaChangeLoop = function (bLoop) {
    if (!checkNone(curAnimationChange)) {
      curAnimationChange.loop = bLoop
    }
  }

