function SelectControl (camera, canvas) {
  // Public variables //////////////////////////////////////////////////
  // 
  this.spriteIntersectList = []
  this.objIntersectList = []

  // Private variables /////////////////////////////////////////////////
  var scope = this
  // 
  var curSelected = null

  // 
  var mouseDownPos = new THREE.Vector2()
  var mouseUpPos = new THREE.Vector2()

  // 
  var raycaster = new THREE.Raycaster()

  // 
  var selectEvent = document.createEvent('HTMLEvents')
  var deselectEvent = document.createEvent('HTMLEvents')
  // THREEJS
  var clickEvent = document.createEvent('HTMLEvents')

  // Public methods ///////////////////////////////////////
  this.setCurSelected = function (sceneObj) {
    curSelected = sceneObj
  }

  this.clearCurselected = function () {
    curSelected = null
  }

  // Private methods ///////////////////////////////////////
  function init () {
    // 
    canvas.addEventListener('mousedown', onMouseDown, false)
    canvas.addEventListener('dblclick', onDoubleClick, false)

    // 
    selectEvent.initEvent('select', false, false)
    deselectEvent.initEvent('deselect', false, false)

    // 
    clickEvent.initEvent('clickIntersect', false, false)
  }

  // ()
  function getIntersects (point) {
    raycaster.setFromCamera(point, camera)

    var index = 0
    // 
    // 
    // for (index = 0; index < scope.spriteIntersectList.length; ++index) {
    //   var sprite = scope.spriteIntersectList[index]
    //   sprite.visible = true
    // }

    // 
    var intersectRes = raycaster.intersectObjects(scope.spriteIntersectList)
    if (checkNone(intersectRes) || intersectRes.length === 0) {
      // 
      // 
      for (index = 0; index < scope.objIntersectList.length; ++index) {
        var sceneObj = scope.objIntersectList[index]
        // sceneObj.visible = true
        // sceneObj.isIntersectBox = true
      }

      intersectRes = raycaster.intersectObjects(scope.objIntersectList)
    }

    return intersectRes
  }

  // 0,01,1
  function getMousePosition (dom, x, y) {
    var rect = dom.getBoundingClientRect()
    return [((x - rect.left) / rect.width) * 2 - 1,
      -((y - rect.top) / rect.height * 2) + 1]
  }

  // 
  function select (object) {
    console.log('Selected: ', object)

    if (object !== null) {
      if (curSelected !== null) {
        // 
        deselectEvent.object = curSelected
        canvas.dispatchEvent(deselectEvent)
      }

      // 
      curSelected = object
      // 
      selectEvent.object = object
      canvas.dispatchEvent(selectEvent)
    } else {
      // , 
      deselectEvent.object = curSelected

      // 
      curSelected = null

      canvas.dispatchEvent(deselectEvent)
    }
  }

  function handleClick () {
    // 
    // console.log(mouseDownPos.distanceTo(mouseUpPos));
    if (mouseDownPos.distanceTo(mouseUpPos) < 0.01) {
      // 
      var intersects = getIntersects(mouseDownPos)
      // 
      if (intersects.length > 0) {
        var object = intersects[0].object
        // isCusReturn
        // cusReturn
        if (object.isCusReturn) { object = object.cusReturn() }

        select(object)
      } else {
        select(null)
      }
    }
  }

  function handleDoubleClick () {
    // 
    var intersects = getIntersects(mouseDownPos)
    // 
    if (intersects.length > 0) {
      var object = intersects[0].object
      // isCusReturn
      // cusReturn
      if (object.isCusReturn) { object = object.cusReturn() }

      select(object)
    } else {
      select(null)
    }
    // 
    clickEvent.object = intersects[0]
    canvas.dispatchEvent(clickEvent)
  }

  function onMouseDown (event) {
    // event.preventDefault()

    // 
    var array = getMousePosition(canvas, event.clientX, event.clientY)
    mouseDownPos.fromArray(array)

    canvas.addEventListener('mouseup', onMouseUp, false)
  }

  function onMouseUp (event) {
    // event.preventDefault()

    // 
    var array = getMousePosition(canvas, event.clientX, event.clientY)
    mouseUpPos.fromArray(array)

    handleClick()
    if (event.button === 2 && Math.abs(mouseUpPos.x - mouseDownPos.x) < 0.00001 && Math.abs(mouseUpPos.y - mouseDownPos.y) < 0.00001) {
      var vEvent = document.createEvent('HTMLEvents')
      vEvent.initEvent('fromEngineRightClick', true, true)
      // 
      vEvent.offsetX = event.offsetX
      vEvent.offsetY = event.offsetY
      vEvent.val = curSelected
      canvas.dispatchEvent(vEvent)
    }

    canvas.removeEventListener('mouseup', onMouseUp, false)
  }

  function onDoubleClick (event) {
    // 
    var array = getMousePosition(canvas, event.clientX, event.clientY)
    mouseDownPos.fromArray(array)
    mouseUpPos.fromArray(array)

    handleDoubleClick()
  }

  init()
}

export { SelectControl }



