function RectAreaLight (parameters) {
  THREE.RectAreaLight.call(this, parameters)

  // 
  DataModelFactory.call(this, DataModelType.RECTAREA_LIGHT)

  this.position.set(0, 0, 0)
  this.updateMatrixWorld()

  // 
  this.controlBall = new LightControlBall(this)
  this.controlBall.position.setFromMatrixPosition(this.matrixWorld)

  // 
  this.targetBox = new THREE.Object3D()
  this.targetBox.translateOnAxis(this.getWorldDirection(), 1.0)

  this.target = this.targetBox
  this.targetBox.updateMatrixWorld()
  this.helper = new RectAreaLightHelper(this)
  this.helper.visible = false

  // 
  this.isCustom = true
  // transformupdate
  this.isNeedUpdate = true

  this.castShadow = false
}

// Constructor
RectAreaLight.prototype = Object.assign(Object.create(THREE.RectAreaLight.prototype), {

  constructor: RectAreaLight,

  // 
  update: function () {
    this.controlBall.setRotationFromQuaternion(this.getWorldQuaternion())
    this.controlBall.position.setFromMatrixPosition(this.matrixWorld)
    this.controlBall.update()

    this.targetBox.position.setFromMatrixPosition(this.matrixWorld)
    this.targetBox.translateOnAxis(this.getWorldDirection(), 1.0)

    this.helper.update()
  }

})

/// /////////////////////////////////////////////////////////////////////

// 
function PointLightHelper (light) {
  THREE.PointLightHelper.call(this, light, 1)
  this.geometry = new THREE.SphereBufferGeometry(1, 16, 16)
}

PointLightHelper.prototype = Object.assign(Object.create(THREE.PointLightHelper.prototype), {

  constructor: PointLightHelper,

  update: function () {
    this.material.color.copy(this.light.color)
    var lightDis = this.light.distance
    this.scale.set(lightDis, lightDis, lightDis)
  }

})

/// ///////////////////////////////////////////////////////////////////////

// 
function SpotLightHelper (light) {
  THREE.SpotLightHelper.call(this, light)
  // 
  this.matrix = light.matrixWorld
  this.position.setFromMatrixPosition(light.matrixWorld)
}

SpotLightHelper.prototype = Object.assign(Object.create(THREE.SpotLightHelper.prototype), {

  update: (function () {
    return function update () {
      this.light.updateMatrixWorld()
      this.position.setFromMatrixPosition(this.light.matrixWorld)

      this.updateMatrixWorld()

      var coneLength = this.light.distance ? this.light.distance : 1000
      var coneWidth = coneLength * Math.tan(this.light.angle)

      this.cone.scale.set(coneWidth, coneWidth, coneLength)

      this.cone.lookAt(new THREE.Vector3(0, 0, 1))

      this.cone.material.color.copy(this.light.color)
    }
  }())

})

/// /////////////////////////////////////////////////////////////////////

function DirectionalLightHelper (light) {
  THREE.DirectionalLightHelper.call(this, light)
  // 
  this.matrix = light.matrixWorld
  this.position.setFromMatrixPosition(light.matrixWorld)
}

DirectionalLightHelper.prototype = Object.assign(Object.create(THREE.DirectionalLightHelper.prototype), {

  constructor: DirectionalLightHelper,

  update: function () {
    var lightPlane = this.children[ 0 ]
    var targetLine = this.children[ 1 ]
    lightPlane.lookAt(new THREE.Vector3(0, 0, 1))
    lightPlane.material.color.copy(this.light.color)
    targetLine.lookAt(new THREE.Vector3(0, 0, 1))
    targetLine.scale.z = 1
  }

})

/// /////////////////////////////////////////////////////////////////////

function RectAreaLightHelper (light) {
  THREE.RectAreaLightHelper.call(this, light)
  // 
  this.matrix = light.matrixWorld
  this.position.setFromMatrixPosition(light.matrixWorld)
}

RectAreaLightHelper.prototype = Object.assign(Object.create(THREE.RectAreaLightHelper.prototype), {

  constructor: RectAreaLightHelper,

  update: function () {
    THREE.RectAreaLightHelper.prototype.update.call(this)
  }

})

/// ////////////////////////////////////////////////////////////////////

export { PointLight, SpotLight, DirectionalLight, RectAreaLight}



