import Vue from 'vue'
import Vuex from 'vuex'
import { engineInterface } from '../engine/core/Engine.js'
// import { pagesList, loadingPageData } from './tempData.js'
// import { pagesList } from './example.js'
import { PageType, TextureType, SelectType, TextAlignType, TextDirectionType, MarkLineType, FontType, HotPointType } from 'Typejs'
import Widget from 'Widget'
// 
import { fontSizes, fontFamily } from './constValue.js'

// 
// curMarkLineId
// curHotPointId
Vue.use(Vuex)

// true false
var ENV = true

// var t = setInterval(() => {
//   if (loadingPageData.curProgress >= 100) {
//     loadingPageData.hasFinished = true
//     clearInterval(t)
//   }
//   loadingPageData.curProgress++
// }, 1)

// UI
var loadingWidget = new Widget()
loadingWidget.marginTop.enable = false
loadingWidget.marginBottom.enable = true
loadingWidget.marginBottom.value = '9.8%'
loadingWidget.marginLeft.value = '50% - width / 2'
loadingWidget.widgetWidth.value = '10%'
loadingWidget.widgetHeight.value = '6.34%'

const store = new Vuex.Store({
  state: {
    // 
    PageType: PageType,
    TextureType: TextureType,
    SelectType: SelectType,
    TextAlignType: TextAlignType,
    TextDirectionType: TextDirectionType,
    MarkLineType: MarkLineType,
    FontType: FontType,
    HotPointType: HotPointType,
    editorWidthInPx: 571,
    editorHeightInPx: 900,

    round: (val, validFixed) => { return validFixed ? parseFloat(val.toFixed(validFixed)) : parseFloat(val.toFixed(2)) },

    fontSizes: fontSizes,
    fontFamily: fontFamily,
    MaxZIndex: 3000,
    MinZIndex: 10,

    runLoad: false,

    // UI
    uiController: {
      width: 0,
      height: 0,
      left: 0,
      top: 0
    },
    // UI
    loadingWidget: loadingWidget,
    // 
    // pagesList: ENV ? engineInterface.pageList : pagesList,
    pagesList: engineInterface.pageList,
    // 
    sceneData: engineInterface.sceneData,
    // pagesList: pagesList,
    // 
    // :
    // curMeshId  curLightId  curPageId
    interface: engineInterface,
    // 
    curActivePageId: 0,
    curSelectMarkLineId: -1,
    curSelectHotPointId: 0,
    // 
    // loadingPageData: ENV ? engineInterface.loadingPageData : loadingPageData,
    loadingPageData: engineInterface.loadingPageData,

    // 
    stopBkgMusic: null
  },
  getters: {
    curMeshId: state => { return state.interface.curMeshId },
    curLightId: state => { return state.interface.curLightId },
    curMarkLineId: state => { return ENV ? state.interface.curMarkLineId : state.curSelectMarkLineId },
    curHotPointId: state => { return ENV ? state.interface.curHotPointId : state.curSelectHotPointId },
    // ()
    curCanvasSelectType: (state, getters) => {
      if (getters.curMeshId !== -1) return state.SelectType.MESH
      if (getters.curLightId !== -1) return state.SelectType.LIGHT
      if (getters.curMarkLineId !== -1) return state.SelectType.MARKLINE
      if (getters.curHotPointId !== -1) return state.SelectType.HOTPOINT
      return state.SelectType.NONE
    },
    curActivePageId: state => { return ENV ? state.interface.curPageId : state.curActivePageId }
  },
  mutations: {
    'update:curMeshId': (state, val) => { state.interface.curMeshId = val },
    'update:curLightId': (state, val) => { state.interface.curLightId = val },
    'update:curMarkLineId': (state, val) => { ENV ? state.interface.curMarkLineId = val : state.curSelectMarkLineId = val },
    // 'update:curHotPointId': (state, val) => { state.interface.curHotPointId = val },
    'update:curHotPointId': (state, val) => {
      ENV ? state.interface.curHotPointId = val : state.curSelectHotPointId = val
      // UI UIUI
      state.curSelectUI.splice(0, state.curSelectUI.length)
    },
    'update:curActivePageId': (state, val) => {
      // 
      ENV ? state.interface.curPageId = val : state.curActivePageId = val
      // var curPageId = ENV ? state.interface.curPageId : state.curActivePageId
      // var curPageType = 0
      // var curPage = null
      // for (var index in state.pagesList) {
      //   if (state.pagesList[index].pageInfo.id === curPageId) {
      //     curPageType = state.pagesList[index].pageInfo.pageType
      //     curPage = state.pagesList[index]
      //   }
      // }
      // 
      // if (curPageType === state.PageType.MATERIAL || curPageType === state.PageType.ANIMATION) {
      //   curPage.pageContent.curSelectBtnId = -1
      // }
    }
  }
})

export { store }




