// UI
var tWidthInPx = innerWidth
var tHeightInPx = innerHeight

var aspect = innerWidth / innerHeight
if (aspect > 0.63492) {
  tWidthInPx = innerHeight * 0.63492
} else {
  tHeightInPx = innerWidth * 1.575
}

// unit: true-px | false-%

function Widget () {
  // private attribute
  // public attribute
  // 
  // px
  this.marginTop = {
    value: '0px',
    enable: true,
    unit: true
  }
  this.marginBottom = {
    value: '0px',
    enable: false,
    unit: true
  }
  this.marginLeft = {
    value: '0px',
    enable: true,
    unit: true
  }
  this.marginRight = {
    value: '0px',
    enable: false,
    unit: true
  }
  this.widgetWidth = {
    value: '10px',
    unit: true
  }
  this.widgetHeight = {
    value: '10px',
    unit: true
  }
  // public function
  // 
  // private function
}

// px
Widget.prototype.regAnalyse = function (exp, refValue, data) {
  // refValue,px
  exp = exp.replace(/\s+/g, '')
  var value = 0
  var array = exp.match(/([+-]?\d+\.?\d*(?:%|px))|([+-]?(width|height)[*/]?\d*\.?\d*)/g)
  if (!array) {
    console.error(': ' + exp + ' ')
    return
  }
  array.forEach(ele => {
    if (ele.match(/width|height/g)) {
      var re = /(\+|-)?(width|height)(\*|\/)?(\d+\.?\d*)?/g.exec(ele)
      var factor = (re[4] === undefined) ? 1 : parseFloat(re[4])
      if (re[1] === '-') factor = factor * -1
      if (re[3] === undefined || re[3] === '*') value += data[re[2]] * factor
      else value += data[re[2]] / factor
    } else {
      var unit = Boolean(/px/.exec(ele))
      var t = parseFloat(ele)
      value += unit ? t : t * refValue / 100
    }
  })
  return value
}

Widget.prototype.calc = function (tw, th) {
  // px
  var re = {
    width: 0,
    height: 0,
    top: 0,
    left: 0
  }
  // 
  if (!this.marginLeft.enable || !this.marginRight.enable) {
    re.width = this.regAnalyse(this.widgetWidth.value, tWidthInPx)
  }
  if (!this.marginTop.enable || !this.marginBottom.enable) {
    re.height = this.regAnalyse(this.widgetHeight.value, tHeightInPx)
  }

  // 
  if (this.marginLeft.enable && this.marginRight.enable) {
    // 
    var leftValueInPx = this.regAnalyse(this.marginLeft.value, innerWidth, re)
    var rightValueInPx = this.regAnalyse(this.marginRight.value, innerWidth, re)
    re.width = innerWidth - (leftValueInPx + rightValueInPx)
  } else {
    // 
    re.width = this.regAnalyse(this.widgetWidth.value, tWidthInPx)
  }
  // 
  if (this.marginTop.enable && this.marginBottom.enable) {
    // 
    var topValueInPx = this.regAnalyse(this.marginTop.value, innerHeight, re)
    var bottomValueInPx = this.regAnalyse(this.marginBottom.value, innerHeight, re)
    re.height = innerHeight - (topValueInPx + bottomValueInPx)
  } else {
    // 
    re.height = this.regAnalyse(this.widgetHeight.value, tHeightInPx)
  }
  // 
  if (this.marginLeft.enable) {
    // 
    re.left = this.regAnalyse(this.marginLeft.value, innerWidth, re)
  } else {
    // 
    re.left = innerWidth - (this.regAnalyse(this.marginRight.value, innerWidth, re) + re.width)
  }
  // 
  if (this.marginTop.enable) {
    // 
    re.top = this.regAnalyse(this.marginTop.value, innerHeight, re)
  } else {
    // 
    re.top = innerHeight - (this.regAnalyse(this.marginBottom.value, innerHeight, re) + re.height)
  }

  return re
}

export default Widget



