function SceneObjData (engine) {
  TransformData.call(this)

  var scope = this

  this._id = 0

  this._name = ''

  bindFunc(this, bindValue, 'id')
  bindFunc(this, bindValue, 'name')
}

SceneObjData.prototype = Object.assign(Object.create(TransformData), {
  constructor: SceneObjData,

  setFunction: function () {
    engineInterface.begindo()

    var sceneManager = new SceneManager()
    sceneManager.setObjName(this._name)

    engineInterface.enddo()
  },

  update: function (sceneObj) {
    TransformData.prototype.update.call(this, sceneObj)

    var sceneManager = new SceneManager()
    var dataObj = sceneObj

    if (checkNone(sceneObj)) {
      if (checkNone(sceneManager.getCurSelected())) { return }
      dataObj = sceneManager.getCurSelected()
      // console.log(dataModel.position);
    }

    this._id = dataObj.sid
    this._name = dataObj.name
  }
})

/// //////////////////////////////////////////////////////////////////////

function CurMaterialData () {
  this._type = 1
  this._matColor = '#ffffff'

  this._curSelectTex = ''
  // this._shininess = 50
  this._roughness = 50
  this._metalness = 50
  this._clearCoat = 0
  this._clearCoatRoughness = 0
  this._reflectivity = 50
  this._opacity = 100

  this.uvData = new UVData()

  bindFunc(this, bindValue, 'type')
  bindFunc(this, bindValue, 'matColor')
  bindFunc(this, bindValue, 'curSelectTex')
  // bindFunc(this, bindValue, 'shininess')
  bindFunc(this, bindValue, 'roughness')
  bindFunc(this, bindValue, 'metalness')
  bindFunc(this, bindValue, 'clearCoat')
  bindFunc(this, bindValue, 'clearCoatRoughness')
  bindFunc(this, bindValue, 'reflectivity')
  bindFunc(this, bindValue, 'opacity')

  this.uvData.setFunction = function () {
    engineInterface.begindo()

    console.log('uv set')
    var sceneManager = new SceneManager()
    sceneManager.setUVTranslate(this._tx, this._ty)
    sceneManager.setUVScale(this._sx, this._sy)

    engineInterface.enddo()
  }

  this.uvData.update = function (sceneObj) {
    var tex = sceneObj.material.map
    if (!checkNone(tex)) {
      this._tx = tex.offset.x
      this._ty = tex.offset.y
      this._sx = tex.repeat.x
      this._sy = tex.repeat.y
    }
  }
}

CurMaterialData.prototype.setFunction = function () {
  engineInterface.begindo()

  var sceneManager = new SceneManager()
  var resourceManager = new ResourceManager()
  // console.log(1111111, this._type)
  switch (this._type) {
    // 
    case TextureType.COLOR:
      sceneManager.setColor(this._matColor)
      break
    // 
    case TextureType.TEXTURE:
      sceneManager.setTexture(resourceManager.getImage(this._curSelectTex))
      break
  }

  // sceneManager.setShininess(this._shininess / 100)
  sceneManager.setRoughness(this._roughness / 100)
  sceneManager.setMetalness(this._metalness / 100)
  sceneManager.setClearCoat(this._clearCoat / 100)
  sceneManager.setClearCoatRoughness(this._clearCoatRoughness / 100)
  sceneManager.setReflectivity(this._reflectivity / 100)
  sceneManager.setObjOpacity(this._opacity / 100)

  engineInterface.enddo()
}

CurMaterialData.prototype.update = function (sceneObj) {
  var dataModel = sceneObj.dataModel

  this._type = dataModel.material.type
  this._matColor = dataModel.material.color

  var resourceManager = new ResourceManager()
  this._curSelectTex = resourceManager.getImage(dataModel.material.textureUrl)
  // this._curSelectTex = dataModel.material.textureUrl
  // this._shininess = (1 - dataModel.shininess) * 100

  this._roughness = dataModel.roughness * 100
  this._metalness = dataModel.metalness * 100
  this._clearCoat = dataModel.clearCoat * 100
  this._clearCoatRoughness = dataModel.clearCoatRoughness * 100
  this._reflectivity = dataModel.reflectivity * 100
  this._opacity = dataModel.opacity * 100

  this.uvData.update(sceneObj)
}

/// /////////////////////////////////////////////////////////////////////////

function CurMeshData (engine) {
  SceneObjData.call(this, engine)

  this.material = new CurMaterialData()

  this.numOfPoints = 0
  this.numOfLines = 0
  this.numOfFaces = 0

  this._pivotMode = false
  this.pivotPos = { _x: 0, _y: 0, _z: 0 }

  var scope = this

  this.pivotPos.setFunction = function () {
    engineInterface.begindo()

    console.log('set pivot pos')
    var sceneManager = new SceneManager()
    sceneManager.setObjPivotPos(
      scope.pivotPos._x,
      scope.pivotPos._y,
      scope.pivotPos._z)

    engineInterface.enddo()
  }

  bindFunc(this, bindValue, 'pivotMode')
  bindFunc(this, this.pivotPos, bindTransform, 'pivotPos', 'x')
  bindFunc(this, this.pivotPos, bindTransform, 'pivotPos', 'y')
  bindFunc(this, this.pivotPos, bindTransform, 'pivotPos', 'z')

  this.engine = engine
}

CurMeshData.prototype = Object.assign(Object.create(SceneObjData.prototype), {
  constructor: CurMeshData,

  setFunction: function () {
    engineInterface.begindo()

    var sceneManager = new SceneManager()

    SceneObjData.prototype.setFunction.call(this)

    sceneManager.setPivotMode(this._pivotMode)

    // 
    engineInterface.updateMeshList()
    // dataInterface.testupdateMeshList();

    engineInterface.enddo()
  },

  update: function () {
    var sceneManager = new SceneManager()
    var sceneObj = sceneManager.getCurSelected()

    if (checkNone(sceneObj) || !sceneObj.isMesh) { return }

    SceneObjData.prototype.update.call(this)

    var geometry = sceneObj.geometry

    this._name = sceneObj.name

    if (geometry.isBufferGeometry) {
      var attributes = geometry.attributes
      var faceNum = attributes.position.count / 3

      this.numOfFaces = faceNum
      this.numOfPoints = faceNum / 2
      this.numOfLines = faceNum * 3
    } else if (geometry.isGeometry) {
      this.numOfFaces = geometry.faces.length
      this.numOfPoints = geometry.vertices.length
      this.numOfLines = this.numOfFaces * 3
    }

    this.material.update(sceneObj)

    this._pivotMode = sceneManager.getTransformControl().pivotMode
    this.pivotPos._x = sceneObj.group.position.x
    this.pivotPos._y = sceneObj.group.position.y
    this.pivotPos._z = sceneObj.group.position.z
  }
})

/// ///////////////////////////////////////////////////////////////////////

function CurLightData () {
  SceneObjData.call(this)

  //  0:/ 1:/ 2: 3:
  this._type = -1
  this._color = '#ffffff'
  this._intensity = 30
  this._decay = 2

  this._shadowBlur = 1
  this._distance = 10
  this._angle = 60

  this._width = 10
  this._height = 10
  this._castShadow = false

  bindFunc(this, bindValue, 'type')
  bindFunc(this, bindValue, 'color')
  bindFunc(this, bindValue, 'intensity')
  bindFunc(this, bindValue, 'decay')

  bindFunc(this, bindValue, 'shadowBlur')
  bindFunc(this, bindValue, 'distance')
  bindFunc(this, bindValue, 'angle')

  bindFunc(this, bindValue, 'width')
  bindFunc(this, bindValue, 'height')
  bindFunc(this, bindValue, 'castShadow')
}

CurLightData.prototype = Object.assign(Object.create(SceneObjData.prototype), {
  constructor: CurLightData,

  setFunction: function () {
    engineInterface.begindo()

    SceneObjData.prototype.setFunction.call(this)

    var sceneManager = new SceneManager()

    // 
    sceneManager.setLightColor(this._color)
    sceneManager.setLightIntensity(this._intensity / 30)
    // sceneManager.setLightIntensity(this._intensity)
    if (checkNone(sceneManager.getCurSelected())) {
      return
    }
    var sceneObj = sceneManager.getCurSelected()

    // 
    switch (this._type) {
      // 
      case 0:
        sceneManager.setLightIntensity(this._intensity / 30 * 2)
        break

        // 
      case 1:
        sceneManager.setLightIntensity(this._intensity / 30 * 2)
        sceneObj.distance = this._distance / 10
        break

        // 
      case 2:
        sceneManager.setLightIntensity(this._intensity / 30 * 10)
        sceneObj.distance = this._distance / 10
        sceneObj.angle = this._angle / 180 * Math.PI
        break

        // 
      case 3:
        sceneManager.setLightIntensity(this._intensity * 2)
        if (this._width === 0) { this._width = 0.01 }
        if (this._height === 0) { this._height = 0.01 }

        sceneObj.width = this._width
        sceneObj.height = this._height
        break
    }

    sceneManager.setLightCastShadow(this._castShadow)

    sceneObj.update()

    // 
    engineInterface.updateLightList()

    engineInterface.enddo()
  },

  update: function () {
    var sceneManager = new SceneManager()
    var sceneObj = sceneManager.getCurSelected()
    if (checkNone(sceneObj) || !sceneObj.isLight) {
      return
    }
    var sceneObj = sceneManager.getCurSelected()

    SceneObjData.prototype.update.call(this)

    if (sceneObj.isPointLight) {
      this._type = 1
      this._distance = sceneObj.distance * 10
    } else if (sceneObj.isSpotLight) {
      this._type = 2
      this._distance = sceneObj.distance * 10
      this._angle = sceneObj.angle * 180 / Math.PI
    } else if (sceneObj.isDirectionalLight) {
      this._type = 0
    } else if (sceneObj.isRectAreaLight) {
      this._type = 3
      this._width = sceneObj.width
      this._height = sceneObj.height
    }

    this._color = '#' + sceneObj.color.getHexString()
    this._intensity = sceneObj.intensity * 30
    // this._intensity = sceneObj.intensity
    if (sceneObj.isDirectionalLight) {
      this._intensity /= 2
    }
    if (sceneObj.isPointLight) {
      this._intensity /= 2
    }
    if (sceneObj.isSpotLight) {
      this._intensity /= 10
    }
    if (sceneObj.isRectAreaLight) {
      this._intensity = sceneObj.intensity / 2
    }

    this._castShadow = sceneObj.castShadow
  }
})

