function UIData () {
  this._id = 0

  this._width = 1.0
  this._height = 1.0
  this._dataX = 1.0
  this._dataY = 1.0

  this._rot = 0
  this._opacity = 1.0

  this._zIndex = 1000

  this.widget = new WidgetData(this)

  bindFunc(this, bindValue, 'id')
  bindFunc(this, bindValue, 'width')
  bindFunc(this, bindValue, 'height')
  bindFunc(this, bindValue, 'dataX')
  bindFunc(this, bindValue, 'dataY')
  bindFunc(this, bindValue, 'rot')
  bindFunc(this, bindValue, 'opacity')
  bindFunc(this, bindValue, 'zIndex')
}

UIData.prototype.setFunction = function () {
  var pageManager = new PageManager()
  var curPage = pageManager.getCurPage()
  if (!checkNone(curPage)) {
    var curUI = curPage.getUIById(this._id)
    if (!checkNone(curUI)) {
      curUI.uiid = this._id
      curUI.width = this._width
      curUI.height = this._height
      curUI.dataX = this._dataX
      curUI.dataY = this._dataY
      curUI.rot = this._rot
      curUI.opacity = this._opacity
      curUI.zIndex = this._zIndex

      curUI.marginTop.value = this.widget.marginTop.value
      curUI.marginTop.enable = this.widget.marginTop._enable
      curUI.marginTop.unit = this.widget.marginTop._unit
      curUI.marginBottom.value = this.widget.marginBottom.value
      curUI.marginBottom.enable = this.widget.marginBottom._enable
      curUI.marginBottom.unit = this.widget.marginBottom._unit
      curUI.marginLeft.value = this.widget.marginLeft.value
      curUI.marginLeft.enable = this.widget.marginLeft._enable
      curUI.marginLeft.unit = this.widget.marginLeft._unit
      curUI.marginRight.value = this.widget.marginRight.value
      curUI.marginRight.enable = this.widget.marginRight._enable
      curUI.marginRight.unit = this.widget.marginRight._unit
      curUI.widgetWidth.value = this.widget.widgetWidth.value
      curUI.widgetWidth.unit = this.widget.widgetWidth._unit
      curUI.widgetHeight.value = this.widget.widgetHeight.value
      curUI.widgetHeight.unit = this.widget.widgetHeight._unit

      console.log('widget', curUI.marginTop.value, this.widget.marginTop.value)
    }
  }
}

UIData.prototype.update = function (uiData) {
  this._id = uiData.uiid
  this._width = uiData.width
  this._height = uiData.height
  this._dataX = uiData.dataX
  this._dataY = uiData.dataY
  this._rot = uiData.rot
  this._opacity = uiData.opacity
  this._zIndex = uiData.zIndex

  this.widget.update(uiData)
}

