function MaterialPageContent (engine) {
  PageContent.call(this, engine)

  this.matBtnList = []

  this._curSelectBtnId = 0
  this._isCameraLock = false

  bindFunc(this, bindValue, 'curSelectBtnId')
  bindFunc(this, bindValue, 'isCameraLock')
}

MaterialPageContent.prototype = Object.assign(Object.create(PageContent.prototype), {
  constructor: MaterialPageContent,

  setFunction: function () {
    var pageManager = new PageManager()
    var page = pageManager.getCurPage()
    var sceneManager = new SceneManager()

    if (!checkNone(page)) {
      engineInterface.begindo()

      engineInterface.saveCurBtnId = this._curSelectBtnId

      var button = pageManager.getCurButton()
      var needApply = false

      if (this._curSelectBtnId < 0) {
        if (!checkNone(button)) { page.cancelButton() }
      } else if (checkNone(button)) {
        needApply = true
      } else {
        needApply = true
        page.cancelButton()
      }

      // console.log(123123, this._curSelectBtnId)
      page.setCurButton(this._curSelectBtnId)

      if (needApply) { page.applyButton() }

      if (this._isCameraLock) {
        sceneManager.lockCamera()
      } else {
        sceneManager.unlockCamera(true)
      }

      engineInterface.enddo()
    }
  },

  update: function (page) {
    PageContent.prototype.update.call(this, page)

    // 
    this.matBtnList.splice(0, this.matBtnList.length)
    var matBtnList = page.dataModel.matBtnList
    for (var index = 0; index < matBtnList.length; ++index) {
      var matBtn = new MaterialButtonData()
      matBtn.update(matBtnList[index])
      this.matBtnList.push(matBtn)
      // matBtnList[index].displayData = matBtn
    }

    if (!checkNone(page)) {
      var button = page.getCurButton()
      if (!checkNone(button)) { this._curSelectBtnId = button.uiid } else { this._curSelectBtnId = -1 }

      this._isCameraLock = page.isCameraLock
    }
  }
})

