function HotPointPageContent (engine) {
  PageContent.call(this, engine)

  this.hotPointList = []
}

HotPointPageContent.prototype = Object.assign(Object.create(PageContent.prototype), {
  constructor: HotPointPageContent,

  setFunction: function () {
  },

  update: function (page) {
    PageContent.prototype.update.call(this, page)

    // 
    for (var index = 0; index < this.hotPointList.length; ++index) {
      var displayData = this.hotPointList[index].hotPointUI
      displayData.imgsData.splice(0, displayData.imgsData.length)
      displayData.textData.splice(0, displayData.textData.length)
      displayData.videoData.splice(0, displayData.videoData.length)
    }
    this.hotPointList.splice(0, this.hotPointList.length)

    for (var sid in page.hotPointList) {
      var data = {}
      var hotPoint = new HotPointData()
      var hotPointUI = new HotPointUIData()

      var sceneObj = page.getHotPointById(sid)
      if (checkNone(sceneObj)) { continue }

      hotPoint.update(sceneObj)

      var pageHotPointUI = page.hotPointList[sid]

      hotPointUI.update(pageHotPointUI)

      data.hotPointData = hotPoint
      data.hotPointUI = hotPointUI
      data.hiddenPageUIId = []

      this.hotPointList.push(data)
    }

    console.log(this.hotPointList)
  }
})

