function PageData (pageType, engine) {
  var scope = this

  this.pageInfo = new PageInfo()

  this.pageUI = new PageUI()

  switch (pageType) {
    case PageType.LOADING:
      this.pageContent = new LoadingPageContent(engine)
      break
    case PageType.BASEFUNC:
      this.pageContent = new BaseFuncPageContent(engine)
      break
    case PageType.MATERIAL:
      this.pageContent = new MaterialPageContent(engine)
      break
    case PageType.MARKLINE:
      this.pageContent = new MarkLinePageContent(engine)
      break
    case PageType.ANIMATION:
      this.pageContent = new AnimationPageContent(engine)
      break
    case PageType.HOTPOINT:
      this.pageContent = new HotPointPageContent(engine)
      break
    case PageType.LINK:
      this.pageContent = new LinkPageContent(engine)
      break
  }
}

PageData.prototype.setFunction = function () {

}

PageData.prototype.update = function (page) {
  this.pageInfo.update(page)
  this.pageUI.update(page)
  this.pageContent.update(page)

  // page.displayData = this
}

/// /////////////////////////////////////////////////////////////////////////

// thisParam, interfaceParam, bindDefineFunc, fstParam, sndParam
// thisParam, bindDefineFunc, param
function bindFunc () {
  var thisParam = arguments[0]

  if (arguments.length === 5) {
    var interfaceParam = arguments[1]
    var bindDefineFunc = arguments[2]
    var fstParam = arguments[3]
    var sndParam = arguments[4]

    var metaBindFunc = bindDefineFunc.bind(thisParam)
    metaBindFunc(interfaceParam, fstParam, sndParam)
  } else if (arguments.length === 4) {
    var bindDefineFunc = arguments[1]
    var fstParam = arguments[2]
    var sndParam = arguments[3]

    var metaBindFunc = bindDefineFunc.bind(thisParam)
    metaBindFunc(fstParam, sndParam)
  } else if (arguments.length === 3) {
    var bindDefineFunc = arguments[1]
    var param = arguments[2]

    var metaBindFunc = bindDefineFunc.bind(thisParam)
    metaBindFunc(param)
  }
}

function bindTransform (interfaceParam, fstParam, sndParam) {
  var paramSetGet = {
    get: function () {
      // if (fstParam === "rotation")
      // console.log("Custom get!");
      // return interfaceParam["_" + sndParam] / Math.PI * 180;
      if (fstParam === 'rotation') {
        return toDecimal(interfaceParam['_' + sndParam] / Math.PI * 180, 2)
      }
      return toDecimal(interfaceParam['_' + sndParam], 2)
    },
    set: function (value) {
      // console.log("Custom set!");
      this['_' + sndParam] = value

      if (fstParam === 'rotation') {
        this['_' + sndParam] = value * Math.PI / 180
      }
      // console.log(this);
      // setFunction
      if (!checkNone(interfaceParam.setFunction)) {
        interfaceParam.setFunction()
      }
    }
  }

  var descriptors = {}
  descriptors[sndParam] = paramSetGet

  Object.defineProperties(this[fstParam], descriptors)
}

// interfaceParam, fstParam, sndParam
// fstParam
function bindValue () {
  if (arguments.length === 1) {
    var param = arguments[0]
    var paramSetGet = {
      get: function () {
        // console.log('Custom get!')

        return this['_' + param]
      },
      set: function (value) {
        console.log(param + ' set ' + value)
        this['_' + param] = value

        // setFunction
        if (!checkNone(this.setFunction)) {
          this.setFunction()
        }
      }
    }

    var descriptors = {}
    descriptors[param] = paramSetGet

    Object.defineProperties(this, descriptors)
  } else if (arguments.length === 3) {
    var interfaceParam = arguments[0]
    var fstParam = arguments[1]
    var sndParam = arguments[2]
    var scope = this

    var paramSetGet = {
      get: function () {
        return interfaceParam['_' + sndParam]
      },
      set: function (value) {
        // console.log("value set!");
        console.log(fstParam + ':' + sndParam + ' set ' + value)
        interfaceParam['_' + sndParam] = value

        // console.log(this);
        // setFunction
        if (!checkNone(scope.setFunction)) {
          scope.setFunction()
        }
      }
    }

    var descriptors = {}
    descriptors[sndParam] = paramSetGet

    Object.defineProperties(this[fstParam], descriptors)
  }
}

