import { DataModelFactory, DataModelType } from '../core/DataModel.js'

function SkyBox (camera, scene) {
  // Public variables ////////////////////////////////////////////////
  this.blur = 0

  // Private variables ////////////////////////////////////////////////
  var scope = this

  // 
  var skyBoxMesh = new THREE.Mesh(new THREE.BoxBufferGeometry(100, 100, 100))
  // 
  var cameraSkyBox = new THREE.PerspectiveCamera(camera.fov, camera.aspect, camera.near, camera.far)
  // 
  var sceneSkyBox = new THREE.Scene()
  // 
  var envMap = new THREE.Texture()
  // 
  var equirectShader = THREE.ShaderLib.equirect
  var skyMat = new THREE.ShaderMaterial({
    fragmentShader: equirectShader.fragmentShader,
    vertexShader: equirectShader.vertexShader,
    uniforms: equirectShader.uniforms,
    depthWrite: false,
    side: THREE.BackSide
  })

  // 
  var envCanvas = null
  var envImage = null

  // Public methods ////////////////////////////////////////////////
  // this.clear = function () {
  //   var ctx = envCanvas.getContext('2d')
  //   ctx.fillStyle = '#BBBBBB'
  //   ctx.fillRect(0, 0, envCanvas.width, envCanvas.height)

  //   envImage = null

  //   clearEnvMap(envCanvas)

  //   scope.blur = 0
  // }

  // 
  this.renderBy = function (renderer, renderTarget) {
    // 
    // 
    cameraSkyBox.rotation.copy(camera.rotation)

    // cameraSkyBox.fov = camera.fov;
    // cameraSkyBox.updateMatrixWorld();
    // cameraSkyBox.updateProjectionMatrix();
    renderer.renderOrigin(sceneSkyBox, cameraSkyBox, renderTarget, false)
  }

  // 
  this.loadEnv = function (envUrl, callback) {
    if (!checkNone(envImage) && envImage.src === envUrl) {
      return
    }

    envCanvas = document.createElement('canvas')
    var ctx = envCanvas.getContext('2d')

    if (envUrl === '') {
      ctx.fillStyle = '#BBBBBB'
      ctx.fillRect(0, 0, envCanvas.width, envCanvas.height)

      envImage = null

      clearEnvMap(envCanvas)
    } else {
      // image
      // 
      envImage = new Image(60, 45)

      envImage.onload = function () {
        envCanvas.width = this.naturalWidth
        envCanvas.height = this.naturalHeight

        // canvasImage
        ctx.drawImage(this, 0, 0)

        setEnvMap(envCanvas)

        if (!checkNone(callback)) { callback() }
      }

      envImage.src = envUrl
    }
  }

  // canvas
  // blur0~180
  this.setEnvBlur = function (blur) {
    if (checkNone(envCanvas) || !skyBoxMesh.visible) {
      console.log('SkyBox, setEnvBlur(), envCanvas: "Need load enviroment map first."')
      return
    }
    if (checkNone(envImage) || envImage.src === '') {
      return
    }

    if (blur >= 0 && blur <= 100) {
      envCanvas.getContext('2d').drawImage(envImage, 0, 0)
      scope.blur = blur - 1
      StackBlur.canvasRGB(envCanvas, 0, 0, envCanvas.width, envCanvas.height, scope.blur)
      // 
      setEnvMap(envCanvas)
    }
  }

  this.setObjEnv = function (sceneObj) {
    if (!checkNone(sceneObj) && sceneObj.isMesh) {
      if (!checkNone(envImage)) {
        sceneObj.material.envMap = envMap
        sceneObj.material.needsUpdate = true
      }
    }
  }

  // Private methods /////////////////////////////////////////////////
  function init () {
    // 
    skyBoxMesh.name = 'skyBox'
    sceneSkyBox.add(skyBoxMesh)
    skyBoxMesh.visible = false

    // 
    envMap.mapping = THREE.EquirectangularReflectionMapping
    envMap.magFilter = THREE.LinearFilter
    envMap.minFilter = THREE.LinearMipMapLinearFilter

    // 
    skyMat.uniforms.tEquirect.value = envMap
    skyBoxMesh.material = skyMat
  }

  // 
  var setEnvMap = function (t_canvas) {
    envMap.image = t_canvas
    envMap.needsUpdate = true

    skyBoxMesh.visible = true
    skyBoxMesh.material.needsUpdate = true

    // 
    // 
    scene.objIntersectList.map(function (x) {
      x.material.envMap = envMap
      x.material.needsUpdate = true
    })
  }

  var clearEnvMap = function (t_canvas) {
    envMap.image = t_canvas
    envMap.needsUpdate = true
    scene.objIntersectList.map(function (x) {
      x.material.envMap = null
      x.material.needsUpdate = true
    })
  }

  init()
}

export { SkyBox }



