import {
  PageType,
  TextureType,
  TextAlignType,
  TextDirectionType,
  MarkLineType,
  FontType,
  UIType,
  HotPointType
} from './Type.js'

// 
var DataModelType = {
  SCENE: 0,
  MESH: 100,
  DIRECT_LIGHT: 200,
  POINT_LIGHT: 201,
  SPOT_LIGHT: 202,
  RECTAREA_LIGHT: 203,
  PAGE: 300,
  LOADING_PAGE: 301,
  BASEFUNC_PAGE: 302,
  MATERIAL_PAGE: 303,
  MARKLINE_PAGE: 304,
  ANIMATION_PAGE: 305,
  HOTPOINT_PAGE: 306,
  HOTPOINT_UI: 316,
  LINK_PAGE: 307,
  MARKLINE: 400,
  HOTPOINT: 500,
  CAMERA: 600,
  ANIMATION: 700,
  ANIMATION_CHANGE: 720,
  KEYFRAME: 800,
  MATERIALBUTTON: 900,
  MATERIAL_CHANGE: 1000,
  IMAGE_UI: 1100,
  TEXT_UI: 1110,
  VIDEO_UI: 1120,
  IMAGE_HYPERLINK: 1201,
  BUTTON_HYPERLINK: 1202,
  PROJECT: 10000
}

// THREEColortoJSONColorjson
THREE.Color.prototype.toJSON = function () {
  return {
    'r': this.r,
    'g': this.g,
    'b': this.b
  }
}

// 
function DataModelFactory (type) {
  // dataModel
  this.dataModel = null

  // this,
  var scope = this

  // 
  switch (type) {
    case DataModelType.SCENE:
      scope.dataModel = new SceneDataModel()
      break
    case DataModelType.MESH:
      scope.dataModel = new MeshDataModel()
      break
    case DataModelType.POINT_LIGHT:
      scope.dataModel = new PointLightDataModel()
      break
    case DataModelType.SPOT_LIGHT:
      scope.dataModel = new SpotLightDataModel()
      break
    case DataModelType.DIRECT_LIGHT:
      scope.dataModel = new DirectLightDataModel()
      break
    case DataModelType.RECTAREA_LIGHT:
      scope.dataModel = new RectAreaLightDataModel()
      break
    case DataModelType.LOADING_PAGE:
      scope.dataModel = new LoadingPageDataModel()
      break
    case DataModelType.BASEFUNC_PAGE:
      scope.dataModel = new BaseFuncPageDataModel()
      break
    case DataModelType.MATERIAL_PAGE:
      scope.dataModel = new MaterialPageDataModel()
      break
    case DataModelType.MARKLINE_PAGE:
      scope.dataModel = new MarkLinePageDataModel()
      break
    case DataModelType.ANIMATION_PAGE:
      scope.dataModel = new AnimationPageDataModel()
      break
    case DataModelType.HOTPOINT_PAGE:
      scope.dataModel = new HotPointPageDataModel()
      break
    case DataModelType.LINK_PAGE:
      scope.dataModel = new LinkPageDataModel()
      break
    case DataModelType.MARKLINE:
      scope.dataModel = new MarkLineDataModel()
      break
    case DataModelType.HOTPOINT:
      scope.dataModel = new HotPointDataModel()
      break
    case DataModelType.CAMERA:
      scope.dataModel = new CameraDataModel()
      break
    case DataModelType.ANIMATION:
      scope.dataModel = new AnimationDataModel()
      break
    case DataModelType.ANIMATION_CHANGE:
      scope.dataModel = new AnimationChangeDataModel()
      break
    case DataModelType.KEYFRAME:
      scope.dataModel = new KeyFrameDataModel()
      break
    case DataModelType.MATERIALBUTTON:
      scope.dataModel = new MaterialButtonDataModel()
      break
    case DataModelType.MATERIAL_CHANGE:
      scope.dataModel = new MaterialChangeDataModel()
      break
    case DataModelType.IMAGE_UI:
      scope.dataModel = new ImageUIDataModel()
      break
    case DataModelType.TEXT_UI:
      scope.dataModel = new TextUIDataModel()
      break
    case DataModelType.VIDEO_UI:
      scope.dataModel = new VideoUIDataModel()
      break
    case DataModelType.HOTPOINT_UI:
      scope.dataModel = new HotPointUIDataModel()
      break
    case DataModelType.IMAGE_HYPERLINK:
      scope.dataModel = new ImageHyperLinkDataModel()
      break
    case DataModelType.BUTTON_HYPERLINK:
      scope.dataModel = new ButtonHyperLinkDataModel()
      break
    case DataModelType.PROJECT:
      scope.dataModel = new ProjectDataModel()
      break
    default:
      break
  }

  scope.dataModel.type = type

  // 
  // bindthreejsposition
  // dataModelthreejs
  // dataModel
  // mesh dataModelmesh
  // dataModel
  // dataModel
  switch (type) {
    case DataModelType.SCENE:
      bindFunc(this, bindParam, 'bkgMusicSrc')
      break
    // 
    case DataModelType.POINT_LIGHT:
    case DataModelType.SPOT_LIGHT:
    case DataModelType.DIRECT_LIGHT:
    case DataModelType.RECTAREA_LIGHT:
      bindFunc(this, bindParam, 'castShadow')
      // 
      bindFunc(this, bindParam, 'color', 'r')
      bindFunc(this, bindParam, 'color', 'g')
      bindFunc(this, bindParam, 'color', 'b')

      // 
      bindFunc(this, bindParam, 'intensity')

    // 
    case DataModelType.MESH:
      bindFunc(this, bindParam, 'orgName')

    case DataModelType.HOTPOINT:
      bindFunc(this, bindParam, 'opacity')
    case DataModelType.MARKLINE:
      bindFunc(this, bindParam, 'sid')

    case DataModelType.CAMERA:
    case DataModelType.KEYFRAME:
      // 
      bindFunc(this, bindName)

      // 
      bindFunc(this, bindParam, 'position', 'x')
      bindFunc(this, bindParam, 'position', 'y')
      bindFunc(this, bindParam, 'position', 'z')
      bindFunc(this, bindParam, 'quaternion', '_x')
      bindFunc(this, bindParam, 'quaternion', '_y')
      bindFunc(this, bindParam, 'quaternion', '_z')
      bindFunc(this, bindParam, 'quaternion', '_w')
      bindFunc(this, bindParam, 'scale', 'x')
      bindFunc(this, bindParam, 'scale', 'y')
      bindFunc(this, bindParam, 'scale', 'z')
      break

    // 

    case DataModelType.BASEFUNC_PAGE:
    case DataModelType.MATERIAL_PAGE:
    case DataModelType.MARKLINE_PAGE:
    case DataModelType.ANIMATION_PAGE:
    case DataModelType.HOTPOINT_PAGE:
      this.pageCamera = this.dataModel.pageCamera

    case DataModelType.LOADING_PAGE:
    case DataModelType.LINK_PAGE:
      bindFunc(this, bindParam, 'pageType')
      bindFunc(this, bindParam, 'pid')
      bindFunc(this, bindParam, 'pageName')
      bindFunc(this, bindParam, 'background')

      this.imgUIList = this.dataModel.imgUIList
      this.textUIList = this.dataModel.textUIList
      break

    case DataModelType.IMAGE_UI:
    case DataModelType.VIDEO_UI:
    case DataModelType.IMAGE_HYPERLINK:
      bindFunc(this, bindParam, 'src')
    case DataModelType.BUTTON_HYPERLINK:
    case DataModelType.TEXT_UI:
      bindFunc(this, bindParam, 'uiid')
      bindFunc(this, bindParam, 'uiType')
      bindFunc(this, bindParam, 'width')
      bindFunc(this, bindParam, 'height')
      bindFunc(this, bindParam, 'dataX')
      bindFunc(this, bindParam, 'dataY')
      bindFunc(this, bindParam, 'rot')
      bindFunc(this, bindParam, 'opacity')
      bindFunc(this, bindParam, 'zIndex')

      bindFunc(this, bindParam, 'marginTop', 'value')
      bindFunc(this, bindParam, 'marginTop', 'enable')
      bindFunc(this, bindParam, 'marginTop', 'unit')

      bindFunc(this, bindParam, 'marginBottom', 'value')
      bindFunc(this, bindParam, 'marginBottom', 'enable')
      bindFunc(this, bindParam, 'marginBottom', 'unit')

      bindFunc(this, bindParam, 'marginLeft', 'value')
      bindFunc(this, bindParam, 'marginLeft', 'enable')
      bindFunc(this, bindParam, 'marginLeft', 'unit')

      bindFunc(this, bindParam, 'marginRight', 'value')
      bindFunc(this, bindParam, 'marginRight', 'enable')
      bindFunc(this, bindParam, 'marginRight', 'unit')

      bindFunc(this, bindParam, 'widgetWidth', 'value')
      bindFunc(this, bindParam, 'widgetWidth', 'unit')

      bindFunc(this, bindParam, 'widgetHeight', 'value')
      bindFunc(this, bindParam, 'widgetHeight', 'unit')

      break

    default:
      break
  }

  // 
  switch (type) {
    case DataModelType.MESH:
      bindFunc(this, bindParam, 'pivotPos', 'x')
      bindFunc(this, bindParam, 'pivotPos', 'y')
      bindFunc(this, bindParam, 'pivotPos', 'z')
      break
    case DataModelType.POINT_LIGHT:
      // 
      bindFunc(this, bindParam, 'distance')
      break

    case DataModelType.SPOT_LIGHT:
      // 
      bindFunc(this, bindParam, 'distance')
      bindFunc(this, bindParam, 'angle')
      break

    case DataModelType.RECTAREA_LIGHT:
      // 
      bindFunc(this, bindParam, 'width')
      bindFunc(this, bindParam, 'height')
      break

    case DataModelType.MARKLINE:
      break

    case DataModelType.HOTPOINT:
      // 
      bindFunc(this, bindParam, 'cameraPos', 'x')
      bindFunc(this, bindParam, 'cameraPos', 'y')
      bindFunc(this, bindParam, 'cameraPos', 'z')
      bindFunc(this, bindParam, 'cameraTarget', 'x')
      bindFunc(this, bindParam, 'cameraTarget', 'y')
      bindFunc(this, bindParam, 'cameraTarget', 'z')

      bindFunc(this, bindParam, 'outsideColor')
      bindFunc(this, bindParam, 'insideColor')
      bindFunc(this, bindParam, 'style')

      // bindFunc(this, bindParam, 'color', 'r')
      // bindFunc(this, bindParam, 'color', 'g')
      // bindFunc(this, bindParam, 'color', 'b')
      break

    case DataModelType.CAMERA:
      // 
      bindFunc(this, bindParam, 'target', 'x')
      bindFunc(this, bindParam, 'target', 'y')
      bindFunc(this, bindParam, 'target', 'z')
      break
    case DataModelType.ANIMATION:
      bindFunc(this, bindParam, 'frameNumber')

      bindFunc(this, bindParam, 'cameraPos', 'x')
      bindFunc(this, bindParam, 'cameraPos', 'y')
      bindFunc(this, bindParam, 'cameraPos', 'z')
      bindFunc(this, bindParam, 'cameraTarget', 'x')
      bindFunc(this, bindParam, 'cameraTarget', 'y')
      bindFunc(this, bindParam, 'cameraTarget', 'z')

      bindFunc(this, bindParam, 'uiid')
      bindFunc(this, bindParam, 'btnName')
      bindFunc(this, bindParam, 'btnType')
      bindFunc(this, bindParam, 'shapeType')
      bindFunc(this, bindParam, 'actShapeColor')
      bindFunc(this, bindParam, 'defShapeColor')
      bindFunc(this, bindParam, 'actTextureUrl')
      bindFunc(this, bindParam, 'defTextureUrl')
      bindFunc(this, bindParam, 'actTextColor')
      bindFunc(this, bindParam, 'defTextColor')
      bindFunc(this, bindParam, 'fontSize')
      bindFunc(this, bindParam, 'opacity')
      bindFunc(this, bindParam, 'text')

      bindFunc(this, bindParam, 'animationMusic')

      this.changeList = this.dataModel.changeList
      break
    case DataModelType.ANIMATION_CHANGE:
      bindFunc(this, bindParam, 'sid')
      bindFunc(this, bindParam, 'loop')
      this.keyFrameList = this.dataModel.keyFrameList
      break

    case DataModelType.KEYFRAME:
      bindFunc(this, bindParam, 'kid')
      bindFunc(this, bindParam, 'opacity')
      bindFunc(this, bindParam, 'timePoint')
      bindFunc(this, bindParam, 'pivotPos', 'x')
      bindFunc(this, bindParam, 'pivotPos', 'y')
      bindFunc(this, bindParam, 'pivotPos', 'z')
      this.labelList = this.dataModel.labelList
      break

    case DataModelType.MATERIALBUTTON:
      // dataModel
      bindFunc(this, bindParam, 'uiid')
      bindFunc(this, bindParam, 'btnName')
      bindFunc(this, bindParam, 'btnType')
      bindFunc(this, bindParam, 'shapeType')
      bindFunc(this, bindParam, 'actShapeColor')
      bindFunc(this, bindParam, 'defShapeColor')
      bindFunc(this, bindParam, 'actTextureUrl')
      bindFunc(this, bindParam, 'defTextureUrl')
      bindFunc(this, bindParam, 'actTextColor')
      bindFunc(this, bindParam, 'defTextColor')
      bindFunc(this, bindParam, 'fontSize')
      bindFunc(this, bindParam, 'opacity')
      bindFunc(this, bindParam, 'text')
      bindFunc(this, bindParam, 'materialChangeList')
      bindFunc(this, bindParam, 'background')
      break

    case DataModelType.MATERIAL_CHANGE:
      bindFunc(this, bindParam, 'sid')
      bindFunc(this, bindParam, 'material')

      this.uvData = this.dataModel.uvData
      break

    case DataModelType.LOADING_PAGE:
      bindFunc(this, bindParam, 'rot')
      bindFunc(this, bindParam, 'style')
      bindFunc(this, bindParam, 'color')
      bindFunc(this, bindParam, 'opacity')
      break
    case DataModelType.BASEFUNC_PAGE:
      break
    case DataModelType.MATERIAL_PAGE:
      // this.curMaterial = this.dataModel.curMaterial
      this.matBtnList = this.dataModel.matBtnList

      bindFunc(this, bindParam, 'isCameraLock')
      break
    case DataModelType.MARKLINE_PAGE:
      this.markLineList = this.dataModel.markLineList
      break
    case DataModelType.ANIMATION_PAGE:
      this.animationList = this.dataModel.animationList
      break
    case DataModelType.HOTPOINT_PAGE:
      this.hotPointList = this.dataModel.hotPointList
      break
    case DataModelType.LINK_PAGE:
      this.imgHyperLinkList = this.dataModel.imgHyperLinkList
      this.btnHyperLinkList = this.dataModel.btnHyperLinkList
      break

    case DataModelType.IMAGE_UI:
      break

    case DataModelType.BUTTON_HYPERLINK:
      bindFunc(this, bindParam, 'btnColor')
    case DataModelType.IMAGE_HYPERLINK:
      bindFunc(this, bindParam, 'url')

    case DataModelType.TEXT_UI:
      bindFunc(this, bindParam, 'text')
      bindFunc(this, bindParam, 'fontSize')
      bindFunc(this, bindParam, 'fontColor')
      bindFunc(this, bindParam, 'textAlign')
      break
    case DataModelType.VIDEO_UI:
      break
    case DataModelType.HOTPOINT_UI:
      this.hpImageUIList = this.dataModel.hpImageUIList
      this.hpTextUIList = this.dataModel.hpTextUIList
      this.hpVideoUIList = this.dataModel.hpVideoUIList
      this.hiddenPageUIList = this.dataModel.hiddenPageUIList
      break
    case DataModelType.PROJECT:
      bindFunc(this, bindParam, 'cover')
      bindFunc(this, bindParam, 'title')
      bindFunc(this, bindParam, 'describe')
      bindFunc(this, bindParam, 'date')
      bindFunc(this, bindParam, 'filePath')
      break
  }
}

/// //////////////////////////////////////////////////////////////////////////////

function DataModel () {
  this.type = 0
}

DataModel.prototype.setFrom = function (jsonObj) {
  this.type = jsonObj.type
}

DataModel.prototype.copy = function (srcModel) {
  this.type = srcModel.type
}

DataModel.prototype.clone = function (srcModel) {
  var dataModel = new DataModel()
  dataModel.copy(this)
  return dataModel
}

DataModel.prototype.equal = function (srcModel) {
  return this.type === srcModel.type
}

/// //////////////////////////////////////////////////////////////////////////////

// 
function TextureDataModel () {
  // Public variables //////////////////////////////////////////////////
  this.type = TextureType.COLOR
  // 
  this.color = '#ffffff'
  // URL
  this.textureUrl = ''
}

TextureDataModel.prototype.setFrom = function (jsonObj) {
  this.type = jsonObj.type
  this.color = jsonObj.color
  this.textureUrl = jsonObj.textureUrl
}

TextureDataModel.prototype.copy = function (srcModel) {
  this.type = srcModel.type
  this.color = srcModel.color
  this.textureUrl = srcModel.textureUrl
}

TextureDataModel.prototype.clone = function (srcModel) {
  var dataModel = new TextureDataModel()
  dataModel.copy(this)
  return dataModel
}

TextureDataModel.prototype.equal = function (srcModel) {
  var res = true

  res = res & (this.type === srcModel.type)
  res = res & (this.color === srcModel.color)
  res = res & (this.textureUrl === srcModel.textureUrl)

  return res
}

/// //////////////////////////////////////////////////////////////////////////////

function PageDataModel () {
  // Parent constructor ////////////////////////////////////////////////////////////
  DataModel.call(this)

  // Public variables //////////////////////////////////////////////////
  this.type = DataModelType.PAGE

  this.pageType = PageType.DEFAULT
  this.pageName = 'default'
  this.pid = 0

  this.background = new TextureDataModel()

  this.background.color = '#bbbbbb'

  this.imgUIList = []
  this.textUIList = []
}

PageDataModel.prototype = Object.assign(Object.create(DataModel), {
  constructor: PageDataModel,

  setFrom: function (jsonObj) {
    DataModel.prototype.setFrom.call(this, jsonObj)
    this.pageType = jsonObj.pageType
    this.pageName = jsonObj.pageName
    // this.pid = jsonObj.pid

    this.background.setFrom(jsonObj.background)
  },

  copy: function (srcModel) {
    DataModel.prototype.copy.call(this, srcModel)
    this.pageType = srcModel.pageType
    this.pageName = srcModel.pageName
    this.pid = srcModel.pid

    this.background.copy(srcModel.background)

    // 
    this.imgUIList.splice(0, this.imgUIList.length)
    for (var index = 0; index < srcModel.imgUIList.length; ++index) {
      this.imgUIList.push(srcModel.imgUIList[index].clone())
    }

    // 
    this.textUIList.splice(0, this.textUIList.length)
    for (var index = 0; index < srcModel.textUIList.length; ++index) {
      this.textUIList.push(srcModel.textUIList[index].clone())
    }
  },

  clone: function (srcModel) {
    var dataModel = new PageDataModel()
    dataModel.copy(this)
    return dataModel
  },

  equal: function (srcModel) {
    var res = DataModel.prototype.equal.call(this, srcModel)

    if (!res) { return res }

    res = res & (this.pageType === srcModel.pageType)
    res = res & (this.pageName === srcModel.pageName)
    res = res & (this.pid === srcModel.pid)

    res = res & (this.background.equal(srcModel.background))

    res = res & (this.imgUIList.length === srcModel.imgUIList.length)
    res = res & (this.textUIList.length === srcModel.textUIList.length)

    return res
  }
})

/// //////////////////////////////////////////////////////////////////////////////

function AnimationDataModel () {
  // Parent constructor ////////////////////////////////////////////////////////////
  DataModel.call(this)

  // Public variables //////////////////////////////////////////////////
  // this.camera = new CameraDataModel()
  this.frameNumber = 1500

  // 
  this.cameraPos = new THREE.Vector3(0, 0, 0)
  this.cameraTarget = new THREE.Vector3(0, 0, 0)

  this.uiid = 0
  this.btnName = 'button'
  // 
  this.btnType = 1

  this.shapeType = 'square'

  this.actShapeColor = '#0999ff'
  this.defShapeColor = '#333333'

  this.actTextureUrl = ''
  this.defTextureUrl = ''

  this.actTextColor = '#ffffff'
  this.defTextColor = '#333333'

  this.fontSize = 0
  this.opacity = 1.0

  this.text = 'default'

  this.changeList = []

  this.animationMusic = ''
}

AnimationDataModel.prototype = Object.assign(Object.create(DataModel), {
  constructor: AnimationDataModel,
  setFrom: function (jsonObj) {
    DataModel.prototype.setFrom.call(this, jsonObj)

    this.frameNumber = jsonObj.frameNumber

    if (!checkNone(jsonObj.cameraPos)) {
      this.cameraPos.x = jsonObj.cameraPos.x
      this.cameraPos.y = jsonObj.cameraPos.y
      this.cameraPos.z = jsonObj.cameraPos.z
    }
    if (!checkNone(jsonObj.cameraTarget)) {
      this.cameraTarget.x = jsonObj.cameraTarget.x
      this.cameraTarget.y = jsonObj.cameraTarget.y
      this.cameraTarget.z = jsonObj.cameraTarget.z
    }

    this.uiid = jsonObj.uiid

    this.btnName = jsonObj.btnName
    this.btnType = jsonObj.btnType // 

    this.shapeType = jsonObj.shapeType

    this.actShapeColor = jsonObj.actShapeColor
    this.defShapeColor = jsonObj.defShapeColor

    this.actTextureUrl = jsonObj.actTextureUrl
    this.defTextureUrl = jsonObj.defTextureUrl

    this.actTextColor = jsonObj.actTextColor
    this.defTextColor = jsonObj.defTextColor

    this.fontSize = jsonObj.fontSize
    this.opacity = jsonObj.opacity

    this.text = jsonObj.text
    // this.camera.setFrom(jsonObj.camera)

    if (!checkNone(jsonObj.animationMusic)) { this.animationMusic = jsonObj.animationMusic }
  },

  copy: function (srcModel) {
    DataModel.prototype.copy.call(this, srcModel)

    this.frameNumber = srcModel.frameNumber

    if (!checkNone(srcModel.cameraPos)) {
      this.cameraPos.x = srcModel.cameraPos.x
      this.cameraPos.y = srcModel.cameraPos.y
      this.cameraPos.z = srcModel.cameraPos.z
    }
    if (!checkNone(srcModel.cameraTarget)) {
      this.cameraTarget.x = srcModel.cameraTarget.x
      this.cameraTarget.y = srcModel.cameraTarget.y
      this.cameraTarget.z = srcModel.cameraTarget.z
    }

    this.uiid = srcModel.uiid

    this.btnName = srcModel.btnName
    this.btnType = srcModel.btnType // 

    this.shapeType = srcModel.shapeType

    this.actShapeColor = srcModel.actShapeColor
    this.defShapeColor = srcModel.defShapeColor

    this.actTextureUrl = srcModel.actTextureUrl
    this.defTextureUrl = srcModel.defTextureUrl

    this.actTextColor = srcModel.actTextColor
    this.defTextColor = srcModel.defTextColor

    this.fontSize = srcModel.fontSize
    this.opacity = srcModel.opacity

    this.text = srcModel.text

    this.changeList.splice(0, this.changeList.length)
    srcModel.changeList.forEach(change => {
      this.changeList.push(change.clone())
    })
    // this.camera.copy(srcModel.camera)

    if (!checkNone(srcModel.animationMusic)) { this.animationMusic = srcModel.animationMusic }
  },

  clone: function (srcModel) {
    var dataModel = new AnimationDataModel()
    dataModel.copy(this)
    return dataModel
  },

  equal: function (srcModel) {
    var res = DataModel.prototype.equal.call(this, srcModel)

    if (!res) { return res }

    res = res & (this.frameNumber === srcModel.frameNumber)

    res = res & (this.cameraPos.x === srcModel.cameraPos.x)
    res = res & (this.cameraPos.y === srcModel.cameraPos.y)
    res = res & (this.cameraPos.z === srcModel.cameraPos.z)
    res = res & (this.cameraTarget.x === srcModel.cameraTarget.x)
    res = res & (this.cameraTarget.y === srcModel.cameraTarget.y)
    res = res & (this.cameraTarget.z === srcModel.cameraTarget.z)

    res = res & (this.uiid === srcModel.uiid)

    res = res & (this.btnName === srcModel.btnName)
    res = res & (this.type === srcModel.btnType) // 

    res = res & (this.shapeType === srcModel.shapeType)

    res = res & (this.actShapeColor === srcModel.actShapeColor)
    res = res & (this.defShapeColor === srcModel.defShapeColor)

    res = res & (this.actTextureUrl === srcModel.actTextureUrl)
    res = res & (this.defTextureUrl === srcModel.defTextureUrl)

    res = res & (this.actTextColor === srcModel.actTextColor)
    res = res & (this.defTextColor === srcModel.defTextColor)

    res = res & (this.fontSize === srcModel.fontSize)
    res = res & (this.opacity === srcModel.opacity)

    res = res & (this.text === srcModel.text)

    // res = res & (this.camera.equal(srcModel.camera))

    if (!checkNone(srcModel.animationMusic)) { res = res & (this.animationMusic === srcModel.animationMusic) }

    return res
  }
})

/// //////////////////////////////////////////////////////////////////////////////

function ModelChangeDataModel () {
  // Parent constructor ////////////////////////////////////////////////////////////
  TransformDataModel.call(this)

  // Public variables //////////////////////////////////////////////////

  this.opacity = 1.0
  this.pivotPos = new THREE.Vector3()
}

ModelChangeDataModel.prototype = Object.assign(Object.create(TransformDataModel), {
  constructor: ModelChangeDataModel,
  setFrom: function (jsonObj) {
    TransformDataModel.prototype.setFrom.call(this, jsonObj)

    this.opacity = jsonObj.opacity

    if (!checkNone(jsonObj.pivotPos)) { this.pivotPos.copy(jsonObj.pivotPos) }
  },

  copy: function (srcModel) {
    TransformDataModel.prototype.copy.call(this, srcModel)

    this.opacity = srcModel.opacity
    if (!checkNone(srcModel.pivotPos)) { this.pivotPos.copy(srcModel.pivotPos) }
  },

  clone: function (srcModel) {
    var dataModel = new ModelChangeDataModel()
    dataModel.copy(this)
    return dataModel
  },

  equal: function (srcModel) {
    var res = TransformDataModel.prototype.equal.call(this, srcModel)

    if (!res) { return res }

    res = res & (this.opacity === srcModel.opacity)

    if (!checkNone(srcModel.pivotPos)) {
      res = res & (this.pivotPos.x === srcModel.pivotPos.x)
      res = res & (this.pivotPos.y === srcModel.pivotPos.y)
      res = res & (this.pivotPos.z === srcModel.pivotPos.z)
    }

    return res
  }
})

