function KeyFrameDataModel () {
  // Parent constructor ////////////////////////////////////////////////////////////
  SceneObjDataModel.call(this)

  // Public variables //////////////////////////////////////////////////
  this.kid = 0
  this.opacity = 1.0
  this.timePoint = 0

  this.labelList = [false, false, false, false]

  this.pivotPos = new THREE.Vector3()
}

KeyFrameDataModel.prototype = Object.assign(Object.create(SceneObjDataModel), {
  constructor: KeyFrameDataModel,
  setFrom: function (jsonObj) {
    SceneObjDataModel.prototype.setFrom.call(this, jsonObj)

    this.kid = jsonObj.kid
    this.opacity = jsonObj.opacity
    this.timePoint = jsonObj.timePoint

    for (var index = 0; index < 4; ++index) {
      this.labelList[index] = jsonObj.labelList[index]
    }

    if (!checkNone(jsonObj.pivotPos)) { this.pivotPos.copy(jsonObj.pivotPos) }
  },

  copy: function (srcModel) {
    SceneObjDataModel.prototype.copy.call(this, srcModel)

    this.kid = srcModel.kid
    this.opacity = srcModel.opacity
    this.timePoint = srcModel.timePoint

    for (var index = 0; index < 4; ++index) {
      this.labelList[index] = srcModel.labelList[index]
    }

    if (!checkNone(srcModel.pivotPos)) { this.pivotPos.copy(srcModel.pivotPos) }
  },

  clone: function (srcModel) {
    var dataModel = new KeyFrameDataModel()
    dataModel.copy(this)
    return dataModel
  },

  equal: function (srcModel) {
    var res = SceneObjDataModel.prototype.equal.call(this, srcModel)

    if (!res) { return res }

    res = res & (this.kid === srcModel.kid)
    res = res & (this.opacity === srcModel.opacity)
    res = res & (this.timePoint === srcModel.timePoint)

    for (var index = 0; index < 4; ++index) {
      res = res & (this.labelList[index] === srcModel.labelList[index])
    }

    if (!checkNone(srcModel.pivotPos)) {
      res = res & (this.pivotPos.x === srcModel.pivotPos.x)
      res = res & (this.pivotPos.y === srcModel.pivotPos.y)
      res = res & (this.pivotPos.z === srcModel.pivotPos.z)
    }

    return res
  }
})

