function MaterialPageDataModel () {
  // Parent constructor ////////////////////////////////////////////////////////////
  PageDataModel.call(this)

  // Public variables //////////////////////////////////////////////////
  this.pageName = 'Material'
  this.pageType = PageType.MATERIAL

  this.pageCamera = new CameraDataModel()
  this.isCameraLock = false

  // this.curMaterial = new TextureDataModel()

  this.matBtnList = []
}

MaterialPageDataModel.prototype = Object.assign(Object.create(PageDataModel), {
  constructor: MaterialPageDataModel,

  setFrom: function (jsonObj) {
    PageDataModel.prototype.setFrom.call(this, jsonObj)
    this.pageCamera.setFrom(jsonObj.pageCamera)
    if (!checkNone(jsonObj.isCameraLock)) { this.isCameraLock = jsonObj.isCameraLock }

    // this.curMaterial.setFrom(jsonObj.curMaterial)

    // // 
    // this.matBtnList.splice(0, this.matBtnList.length)
    // for (var index = 0; index < jsonObj.matBtnList.length; ++index) {
    //   this.matBtnList.push(jsonObj.matBtnList[index].clone())
    // }
  },

  copy: function (srcModel) {
    PageDataModel.prototype.copy.call(this, srcModel)

    this.pageCamera.copy(srcModel.pageCamera)
    // this.curMaterial.copy(srcModel.curMaterial)

    // 
    this.matBtnList.splice(0, this.matBtnList.length)
    for (var index = 0; index < srcModel.matBtnList.length; ++index) {
      this.matBtnList.push(srcModel.matBtnList[index].clone())
    }

    if (!checkNone(srcModel.isCameraLock)) { this.isCameraLock = srcModel.isCameraLock }
  },

  clone: function (srcModel) {
    var dataModel = new MaterialPageDataModel()
    dataModel.copy(this)
    return dataModel
  },

  equal: function (srcModel) {
    var res = PageDataModel.prototype.equal.call(this, srcModel)

    if (!res) { return res }

    res = res & (this.pageCamera.equal(srcModel.pageCamera))
    // res = res & (this.curMaterial.equal(srcModel.curMaterial))

    res = res & (this.matBtnList.length === srcModel.matBtnList.length)

    if (!checkNone(srcModel.isCameraLock)) { res = res & (this.isCameraLock === srcModel.isCameraLock) }

    return res
  }
})

