HotPointUIDataModel.prototype = Object.assign(Object.create(DataModel), {
  constructor: HotPointUIDataModel,

  setFrom: function (jsonObj) {
    DataModel.prototype.setFrom.call(this, jsonObj)
  },

  copy: function (srcModel) {
    DataModel.prototype.copy.call(this, srcModel)

    // 
    this.hpImageUIList.splice(0, this.hpImageUIList.length)
    for (var index = 0; index < srcModel.hpImageUIList.length; ++index) {
      this.hpImageUIList.push(srcModel.hpImageUIList[index].clone())
    }

    // 
    this.hpTextUIList.splice(0, this.hpTextUIList.length)

    for (var index = 0; index < srcModel.hpTextUIList.length; ++index) {
      this.hpTextUIList.push(srcModel.hpTextUIList[index].clone())
    }

    // 
    this.hpVideoUIList.splice(0, this.hpVideoUIList.length)
    for (var index = 0; index < srcModel.hpVideoUIList.length; ++index) {
      this.hpVideoUIList.push(srcModel.hpVideoUIList[index].clone())
    }

    // 
    this.hiddenPageUIList.splice(0, this.hiddenPageUIList.length)
    for (var index = 0; index < srcModel.hiddenPageUIList.length; ++index) {
      this.hiddenPageUIList.push(srcModel.hiddenPageUIList[index])
    }
  },

  clone: function (srcModel) {
    var dataModel = new HotPointUIDataModel()
    dataModel.copy(this)
    return dataModel
  },

  equal: function (srcModel) {
    var res = DataModel.prototype.equal.call(this, srcModel)

    if (!res) { return res }

    res = res & (this.hpImageUIList.length === srcModel.hpImageUIList.length)
    res = res & (this.hpTextUIList.length === srcModel.hpTextUIList.length)
    res = res & (this.hpVideoUIList.length === srcModel.hpVideoUIList.length)
    res = res & (this.hiddenPageUIList.length === srcModel.hiddenPageUIList.length)

    return res
  }
})

